@mixin mobile{
    @media screen and (max-width:480px){
        @content
    }
}

@mixin tablet{
    @media screen and (max-width:768px){
        @content
    }
}

@mixin laptop{
    @media screen and (max-width:1200px){
        @content
    }
}

.formContainer{
    background-color: rgb(180, 219, 249);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .formWrapper{
        background-color: white;
        padding: 20px 60px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;

        .logo{
            color: rgb(227, 139, 9);
            font-weight: bold;
            font-size: 24px;
          
        }
        .title{
            color: rgb(233, 162, 9);
            font-size: medium;
        }
       form{
        display: flex;
        flex-direction: column;
        gap: 15px;
        

        input{
            border: none;
            border-bottom: 1px solid rgb(180, 219, 249);
            padding: 15px;
            width: 250px;
       &::placeholder{
        color: rgb(207, 182, 70);
       }     
        }
        button{
            background-color: rgb(148, 148, 181);
            color: white;
            padding: 10px;
            font-weight: bold;
            border: none;
            cursor: pointer;
        }

        label{
             display: flex;
             align-items: center;
             gap: 10px;
             color: rgb(239, 163, 101);
             font-size: 12px;
             cursor: pointer;

             img{
                width: 32px;
             }
        }
       }
       p{
        color: rgb(48, 14, 14);
        font-size: 12px;
        margin-top: 10px;
       }
    }
}

.home{
    background-color: rgb(180, 219, 249);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .container{
        border: 1px solid white;
        border-radius: 10px;
        width: 75%;
        height: 80%;
        display: flex;
         overflow: hidden;
         @include tablet{
            width: 90%;
        }
        .sidebar{ flex: 1; border-right: 1px solid #3e3c61;
        background-color:#3e3c61 ;
        position: relative;

        .navbar{
            display: flex;
            align-items: center;
            background-color: #2f2d52;
            height: 50px;
            padding: 10px;
            justify-content: space-between;
            color: #ddddf7;   

            .logo{
                font-weight: bold;
                @include tablet{
                    display:none
                }
            }

            .user{
                display: flex;
                gap: 10px;

                img{
                    background-color: #ddddf7;
                    height: 24px;
                    width: 24px;
                    border-radius: 50%;
                    object-fit: cover;
                }

                button{
                    background-color:   #5d5b8b;
                    color: #ddddf7;
                    font-size: 10px;
                    border: none;
                    cursor: pointer;
                    @include tablet{
                       position: absolute;
                       bottom: 10px;
                    }
                }
            }

           
             }
             .search{
                border-radius: 1px solid gray;
                border-bottom: 1px solid #5d5b8b;
                .searchform{
                padding: 10px;
                    input{
                        background-color: transparent;
                        border: none;
                        color: white;
                        outline: none;

                        &::placeholder{
                            color: lightgray;
                        }
                    }
                }
            }
            
            .userChat{
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: white;
                cursor: pointer;

                &:hover{
          background-color: #2f2d52;
                }
                img{
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    // object-fit: cover;
                }
                .userchatInfo{
                    span{font-size: 18px; font-weight: 500;}
                    p{font-size: 14px; color: lightgray;}
                }
            }
    }
        .chat{flex: 2;
        
            .chatInfo{
                height: 50px;
                background-color: #5d5b8b;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;
             color: lightgray;
            }
            .chaticons{
                display: flex;
                gap: 10px;

                img{
                    height: 24px;
                    cursor: pointer;
                }
            }
            .messages{
                background-color: #ddddf7;
                padding: 10px;
                height: calc(100% - 160px);
                overflow-y: scroll;
                .message{
                    display: flex;
                    gap: 20px;
                    margin-bottom: 20px;
                    .messageInfo{
                        display: flex;
                        flex-direction: column;
                        color: gray;
                        font-weight: 300;
                       img{
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    }
                    .messageContent{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;


                        p{
                            background-color: white;
                            padding: 10px 20px;
                            border-radius: 0px 10px 10px 10px;
                            max-width: max-content;
                        }
                        img{
                            width: 50%;
                        }
                    }
                  &.owner{
                    flex-direction: row-reverse;
                    .messageContent{
                        align-items: flex-end;
                  
                    p{
                        background-color: #8da4f1;
                        color: white;
                        border-radius: 10px 0px 10px 10px;

                    }
                }
                  }
                
                }

            }

            .input{
                height: 50px;
                background-color: white;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;


                input{
                    width: 100%;
                    border: none;
                    outline: none;
                    color: #2f2d52;
                    font-size: 18px;

                    &::placeholder{
                        color: lightgray;
                    }
                }

                .send{
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    img{
                        height: 24px;
                        cursor: pointer;
                    }

                    button{
                         border: none;
                         padding: 10px 15px;
                         color: white;
                         background-color: #4869d3;
                         cursor: pointer;
                    }
                }
            }
        }
    }
}

